body{
  background-color: #4F4A45;
  display: flex;
  justify-content: center;
}
.to-do-list{
  font-family: Arial, sans-serif;
  text-align: center;
  
}
h1{
  font-size: 4rem;
  color: whitesmoke;
}
.main{
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 8px solid #6C5F5B;
  width: 750px;
  padding: 20px;
}
.input-task{
  width: 600px;
}
button{
  font-size: 1.7rem;
  font-weight: bold;
  padding: 10px 20px;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.5s ease;
}
.add-button{
  background-color: hsl(105, 99%, 48%);
  padding-left: 10px;
}
.add-button:hover{
  background-color: hsl(105, 99%, 20%);
}
.delete-button{
  background-color: hsl(0, 100%, 50%);
}
.delete-button:hover{
  background-color: hsl(0, 100%, 20%);
}
.move-button{
  background-color: hsla(0, 0%, 0%, 0.24);
}
.move-button:hover{
  background-color: hsla(103, 100%, 50%, 20%);
}
input[type="text"]{
  font-size: 1.6rem;
  padding: 10px;
  border: 2px solid hsla(0, 0, 80%, 0.5);
  border-radius: 5px;
  color: hsla(0, 0, 0, 0.5);
}
.order-list{
  display: flex;
  justify-content: center;
}
ol{
  padding: 0;
  width: 800px;
}
li{
  font-size: 2rem;
  font-weight: bold;
  padding: 15px;
  background-color: hsl(0, 0%, 97%);
  margin-bottom: 10px;
  border: 3px solid hsla(0, 0%, 85%, 0.75);
  border-radius: 5px;
  display: flex;
  align-items: center;
}
.text{
  flex: 1;
  border: 2px solid black;
}
.delete-button, .move-button{
  padding: 8px 12px;
  font-size: 1.4rem;
  margin-left: 10px;
}